export default {
	onLoad() {
		this.initSystemInfo()
		this.handleImgOpa ()
		
	},
	
	onHide() {
		this.timer = null
	},
	data () {
		return {
			bgImg: 'data:image/png;base64,',
			fullPageHeight: 0, // 屏幕高
			imgOpacity: 1, // 动画opacity
			timer: null, // 定时器
			animationData: {}, //　动画
			animation: null, // 动画
			maskShow: false
		}
	},
	methods: {
		// 获取系统信息
		initSystemInfo () {
			uni.getSystemInfo({
			    success: (res) => {
					this.fullPageHeight = res.windowHeight
			    }
			});
		},
		// 修改opacity
		handleImgOpa () {
			// this.timer = setInterval(() => {
			// 	this.imgOpacity = this.imgOpacity - 0.3
			// 	if (this.imgOpacity <= 0.2) {
			// 		this.imgOpacity = 1
			// 	}
			// }, 600)
			
			this.timer = setInterval(() => {
				this.animation = uni.createAnimation({
				  duration: 700,
				  timingFunction: "linear",
				  delay: 0
				})
				this.imgOpacity -= 0.5
				if (this.imgOpacity <= 0.1) {
					this.imgOpacity = 1
				}
				this.animation.opacity(this.imgOpacity).step()
				this.animationData = this.animation.export()
			}, 1000)
			
		},
		// 下载
		goToDownLoad (index) {
			this.maskShow = true
	
		},
		hideMask () {
			this.maskShow = false
		}
	}
}
